@font-face {
  font-family: "Oswald";
  src: url(/fonts/Oswald-VariableFont_wght.ttf);
}
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
  min-width: 320px;
  font-family: "Oswald", Arial, sans-serif;
  box-sizing: border-box;
  color: #111111;
  background-color: #ffffff;
  overflow-x: hidden;
}
body a {
  text-decoration: none;
}
body ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.button {
  padding: 10px 30px 10px 25px;
  min-width: 245px;
  min-height: 40px;
  color: #ffffff;
  background-color: #68b738;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  opacity: 0.3;
}

.page-header {
  position: relative;
  min-height: 264px;
}
.page-header__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
.page-header__logo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 66px;
}
.page-header__logo-mobile {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .page-header {
    min-height: 77px;
  }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 24px 30px;
  }
  .page-header__logo {
    display: block;
    width: auto;
    height: auto;
  }
  .page-header__logo:hover {
    opacity: 0.8;
  }
  .page-header__logo:active {
    opacity: 0.6;
  }
  .page-header__logo img {
    width: 178px;
    height: 58px;
  }
  .page-header__logo-name-mobile {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 720px;
    height: 696px;
    background: url(/img/index-background-desktop.jpg.jpg) 50% no-repeat;
  }
  .page-header__wrapper {
    padding: 50px 110px 0;
  }
  .page-header__logo img {
    width: 202px;
    height: 59px;
  }
}

.main-navigation {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin-left: -20px;
  margin-right: -20px;
}
.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 20px;
}
@media (min-width: 768px) {
  .main-navigation {
    flex-direction: row;
  }
  .main-navigation__toggle {
    display: none;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.site-list__item {
  width: 100%;
  min-height: 66px;
  padding: 23px 0;
  text-align: center;
  border-bottom: 1px solid;
  border-bottom-color: #e6e6e6;
}
.site-list__item:first-child {
  margin-left: 0;
  border-top: 1px solid;
  border-top-color: #e6e6e6;
}
.site-list__link {
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.site-list__link:hover {
  opacity: 60%;
}
.site-list__link:active {
  opacity: 30%;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
  }
  .site-list__item {
    width: auto;
    min-height: 42px;
    margin-left: 33px;
    padding: 12px 0;
    border-bottom: none;
  }
  .site-list__item--active {
    border-bottom: 2px solid;
    border-bottom-color: #68b738;
  }
  .site-list__item:first-child {
    border-top: none;
  }
  .site-list__link {
    font-size: 18px;
    line-height: 24.86px;
    color: #222222;
  }
}
@media (min-width: 1440px) {
  .site-list__item {
    width: auto;
    margin-left: 36px;
    border-bottom: none;
  }
  .site-list__item--active {
    border-bottom: 2px solid;
    border-bottom-color: #ffffff;
  }
  .site-list__link {
    font-size: 20px;
    line-height: 24.86px;
    color: #ffffff;
  }
}

.page-main {
  background: url(/img/index-background-mobile.jpg) top center no-repeat;
  background-size: 100% auto;
}
@media (min-width: 768px) {
  .page-main {
    background: none;
  }
}

.programms {
  min-height: 1055px;
  min-width: 320px;
