.example {
  padding-top: 30px;
  background-color: #f2f2f2;
}
.example__title {
  margin-top: 0;
  margin-bottom: 35px;
  font-size: 36px;
  line-height: 60px;
  font-weight: inherit;
}
.example__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  min-width: 320px;
}
.example__description-wrapper {
  width: 100%;
  background-color: #f2f2f2;
