.slider {
  width: 100%;
  min-width: 200px;
}
.slider__before-toggle, .slider__after-toggle {
  display: block;
  padding-bottom: 1px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.slider__before-toggle {
  order: -1;
}
.slider__input {
  appearance: none;
  -webkit-appearance: none;
  margin: auto 19px;
  padding: 6px 4px;
  width: 25%;
  min-width: 84px;
  height: 14px;
  border-radius: 25px;
  border: 1px solid;
  border-color: #cdcdcd;
}
.slider__input::-webkit-slider-thumb {
  appearance: none;
  -webkit-appearance: none;
  width: 50%;
  height: 6px;
  border-radius: 25px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .slider__before-toggle, .slider__after-toggle {
    padding-bottom: 5px;
    border-bottom: 1px dotted;
    border-bottom-color: #bebebe;
    font-size: 20px;
  }
  .slider__input {
    margin: auto 23px;
    padding: 0;
    width: 60%;
    height: 7px;
    border: none;
    background-color: #dcdcdc;
    pointer-events: none;
  }
  .slider__input::-webkit-slider-thumb {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 12px solid;
    border-color: #ffffff;
    box-sizing: border-box;
    box-shadow: 0px 0px 0px 1px #cdcdcd, 0px 2px 5px 0px rgba(0, 0, 0, 0.15);
    pointer-events: auto;
  }
  .slider__input::-webkit-slider-thumb:hover {
    box-shadow: 0px 0px 0px 1px #cdcdcd, 0px 3px 5px 0px rgba(0, 0, 0, 0.25);
  }
  .slider__input::-webkit-slider-thumb:active {
    width: 30px;
    height: 30px;
    border: 11px solid;
    border-color: #ffffff;
    box-shadow: 0px 0px 0px 2px #68b738, 0px 4px 5px 0px rgba(0, 0, 0, 0.3);
  }
  .slider #before-after:hover ~ .slider__before-toggle,
  .slider #before-after:hover ~ .slider__after-toggle {
    border-bottom-style: solid;
  }
  .slider #before-after:active ~ .slider__before-toggle,
  .slider #before-after:active ~ .slider__after-toggle {
    border-bottom-color: transparent;
  }
}
@media (min-width: 1440px) {
  .slider {
    width: 570px;
