  .slider__before-toggle {
    width: 45px;
    margin-right: 25px;
    text-align: left;
  }
  .slider__after-toggle {
    width: 50px;
    margin-left: 22px;
    text-align: right;
  }
  .slider__input {
    width: 474px;
    margin-left: 0;
    margin-right: 0;
  }
}

